$color_2: #57585c;
$color_3: var(--color-text);
$color_4: var(--color-link);
$color_5: var(--color-link-hover);
$color_6: var(--color-info);
$color_7: transparent;
$color_8: #000000;
$color_9: var(--color-bg);
$color_10: #ffffff;
$color_11: #19e218;
$font_family_1: sans-serif;
$font_family_2: monospace,
serif;
$font_family_3: inherit;
$font_family_4: "Buenard",
serif;
$font_family_5: "Gloria Hallelujah",
cursive;
