sans-serif;
$background_color_1: #fff;
$background_color_2: var(--color-bg);
@import "https://fonts.googleapis.com/css?family=Anton|Buenard:400,700";
@import "https://fonts.googleapis.com/css?family=Gloria+Hallelujah|Satisfy";

/* Page Loader */

@mixin keyframes($animationName) {
    @-webkit-keyframes #{$animationName} {
        @content;
    }
    @-moz-keyframes #{$animationName} {
        @content;
    }
    @-o-keyframes #{$animationName} {
        @content;
    }
    @keyframes #{$animationName} {
        @content;
    }
}

@include keyframes(loaderAnim) {
    to {
        opacity: 1;
        transform: scale3d(0.5, 0.5, 1);
    }
}


