}

figcaption {
    display: block;
}

figure {
    display: block;
    margin: 0;
}

footer {
    display: block;
}

header {
    display: block;
}

hgroup {
    display: block;
}

main {
    display: block;
    position: relative;
}

nav {
    display: block;
}

section {
    display: block;
}

summary {
    display: block;
}

audio {
