}

body {
    margin: 0;
    --color-text: #000;
    --color-bg: #cb287b;
    --color-link: #000;
    --color-link-hover: #000;
    --color-info: #000;
    font-family: $font_family_4;
    min-height: 100vh;
    color: $color_2;
    color: $color_3;
    background: #9ad18b;
    // background-color: $background_color_1;
    // background-color: $background_color_2;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    padding: 20px;
    padding-bottom: 0px;
    position: relative;
}

a {
    &:focus {
        outline: thin dotted;
        color: $color_5;
        outline: none;
    }
    &:active {
        outline: 0;
    }
    // &:hover {
    // 	outline: 0;
    // 	color: $color_5;
    // 	outline: none;
    // }
    text-decoration: none;
    color: $color_4;
    outline: none;
}

h1 {
    font-size: 2em;
    margin: 0.67em 0;
}

abbr[title] {
    border-bottom: 1px dotted;
}

b {
    font-weight: bold;
}

strong {
    font-weight: bold;
}

dfn {
    font-style: italic;
}

hr {
    -moz-box-sizing: content-box;
    box-sizing: content-box;
    height: 0;
}

mark {
    background: #ff0;
    color: $color_1;
}

code {
    font-family: $font_family_2;
    font-size: 1em;
}

kbd {
    font-family: $font_family_2;
    font-size: 1em;
}

pre {
    font-family: $font_family_2;
    font-size: 1em;
    white-space: pre-wrap;
}

samp {
    font-family: $font_family_2;
    font-size: 1em;
}

q {
    quotes: "\201C""\201D""\2018""\2019";
}

small {
    font-size: 80%;
}

sub {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
    bottom: -0.25em;
}

sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
    top: -0.5em;
}

img {
    border: 0;
}

svg {
    &:not(:root) {
        overflow: hidden;
    }
}

fieldset {
    border: 1px solid #c0c0c0;
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em;
}

legend {
    border: 0;
    padding: 0;
}

button {
    font-family: $font_family_3;
    font-size: 100%;
    margin: 0;
    line-height: normal;
    text-transform: none;
    -webkit-appearance: button;
    cursor: pointer;
    &::-moz-focus-inner {
        border: 0;
        padding: 0;
    }
}

input {
    font-family: $font_family_3;
    font-size: 100%;
    margin: 0;
    line-height: normal;
    &::-moz-focus-inner {
        border: 0;
        padding: 0;
    }
}

select {
    font-family: $font_family_3;
    font-size: 100%;
    margin: 0;
    text-transform: none;
}

textarea {
    font-family: $font_family_3;
    font-size: 100%;
    margin: 0;
    overflow: auto;
    vertical-align: top;
}

input[type="reset"] {
    -webkit-appearance: button;
    cursor: pointer;
}

input[type="submit"] {
    -webkit-appearance: button;
    cursor: pointer;
}

button[disabled] {
    cursor: default;
}

input[type="checkbox"] {
    box-sizing: border-box;
    padding: 0;
}

input[type="radio"] {
    box-sizing: border-box;
    padding: 0;
}

input[type="search"] {
    -webkit-appearance: textfield;
    -moz-box-sizing: content-box;
    -webkit-box-sizing: content-box;
    box-sizing: content-box;
    &::-webkit-search-cancel-button {
        -webkit-appearance: none;
    }
    &::-webkit-search-decoration {
        -webkit-appearance: none;
    }
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

* {
    box-sizing: border-box;
    &::after {
        box-sizing: border-box;
    }
    &::before {
        box-sizing: border-box;
    }
}

.preview-open {
    overflow: hidden;
}

.js {
    .loading {
        &::before {
            content: "";
            position: fixed;
            z-index: 100000;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: var(--color-bg);
        }
        &::after {
            content: "";
            position: fixed;
            z-index: 100000;
            top: 50%;
            left: 50%;
            width: 60px;
            height: 60px;
            margin: -30px 0 0 -30px;
            pointer-events: none;
            border-radius: 50%;
            opacity: 0.4;
            background: var(--color-link);
            animation: loaderAnim 0.7s linear infinite alternate forwards;
        }
    }
}

.hidden {
    position: absolute;
    overflow: hidden;
    width: 0;
    height: 0;
    pointer-events: none;
}

.icon {
    display: block;
    width: 1.5em;
    height: 1.5em;
    fill: currentColor;
}

.content {
    position: relative;
    padding-top: 14px;
    // background-image: url("../img/fondo.png");
    // background-repeat: repeat;
    background: #cca69a;
}

.title-logo {
    margin: 0;
    a {
        width: 140px;
        margin: auto;
        display: block;
    }
    img {
        margin: auto;
        display: block;
        width: 140px;
        margin-top: 14px;
        border-radius: 50%;
    }
}

.sub_title {
    font-family: "IBM Plex Mono", monospace;
    padding: 1.5rem 3rem 0rem 3rem;
    margin: 0;
    font-weight: 500;
}

.codrops-header {
    position: relative;
    z-index: 100;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    align-items: center;
    padding: 2rem;
}

.codrops-header__title {
    font-size: 1em;
    font-weight: bold;
    margin: 0;
    padding: 0;
}

.info {
    margin: 0 0 0 1.25em;
    color: $color_6;
}

.github {
    display: block;
    margin: 0 0 0 auto;
}

.codrops-links {
    position: relative;
    display: flex;
    justify-content: center;
    margin: 0 1em 0 0;
    text-align: center;
    white-space: nowrap;
}

.codrops-icon {
    display: inline-block;
    margin: 0.15em;
    padding: 0.25em;
}

.grid {
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;
    padding-bottom: 10rem;
    position: relative;
    top: 4rem;
}

.grid__item {
    display: block;
    margin: 0 0 8rem 0;
    cursor: pointer;
    &:hover {
        .box__title-inner {
            &::before {
                height: 100%;
                width: 100%;
            }
        }
        .box__img {
            &:not(.box__img--original) {
                filter: grayscale(0);
            }
        }
        .box__text-inner--reverse.even {
            color: $color_9;
        }
        .box__text-inner--reverse.odd {
            color: $color_11;
        }
    }
}

.box {
    position: relative;
    margin: 2rem;
}

.box__title {
    margin: 0;
    line-height: 1;
    position: absolute;
    z-index: 100;
}

.box__title-inner {
    display: block;
    position: relative;
    font-weight: normal;
    text-transform: uppercase;
    font-size: 3.15rem;
    letter-spacing: 0.15rem;
    line-height: 1.05;
    font-family: $font_family_6;
    &::before {
        content: attr(data-hover);
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 0;
        overflow: hidden;
        -webkit-text-stroke: 0;
        -webkit-text-fill-color: #000;
        color: $color_8;
        transition: all 0.2s;
    }
}

.box__title-inner_four {
    font-size: 2.55rem;
}

.box__title-inner-gris {
    font-size: 3.15rem;
}

.box__title-inner_image_one {
    font-size: 2.5rem;
}

.box__title-inner[data-hover] {
    -webkit-text-stroke: 2px #000;
    -webkit-text-fill-color: transparent;
    color: $color_7;
}

.box__text {
    margin: 0;
    position: absolute;
    top: -4rem;
    font-weight: normal;
    text-transform: uppercase;
    letter-spacing: 0.15rem;
    font-size: 1.2rem;
    font-family: $font_family_6;
}

.box__text-inner {
    position: relative;
    display: block;
    border: 6px solid var(--color-text);
    padding: 0.25rem;
}

.box__text-inner--reverse {
    color: #fff;
    background: var(--color-text);
}

.box__img {
    display: block;
    flex: none;
    margin: 0 auto;
    max-width: 100%;
    filter: grayscale(1);
    transition: filter 0.4s;
}

.box__img_image_two {
    max-width: 80%;
}

.box__shadow {
    position: absolute;
    width: 100%;
    height: 100%;
    top: -1rem;
    left: -1rem;
    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAKCAYAAACNMs+9AAAAOklEQVQoU43MSwoAMAgD0eT+h7ZYaOlHo7N+DNHL2HAGgBWcyGcKbqTghTL4oQiG6IUpOqFEC5bI4QD8PAoKd9j4XwAAAABJRU5ErkJggg==);
}

.box__deco {
    font-size: 2.4rem;
    line-height: 1;
    // font-weight: bold;
    position: absolute;
    bottom: -4rem;
    right: -1rem;
    display: none;
}

.box__content {
    position: absolute;
    max-width: 195px;
    font-size: 0.9rem;
    text-align: right;
    strong {
        white-space: nowrap;
    }
}

.overlay {
    pointer-events: none;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
}

.overlay--open {
    pointer-events: auto;
}

.overlay__reveal {
    width: 100%;
    top: 0;
    left: 0;
    background: #000;
    position: absolute;
    z-index: 9999999;
    transform: translate3d(100%, 0, 0);
}

.overlay__item {
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    display: block;
    flex-direction: column;
    align-items: center;
    padding: 20px 5vw;
    justify-content: center;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    .box {
        margin: 0;
    }
    .box__title {
        bottom: 0;
    }
}

#preview-1 {
    background-image: -webkit-gradient( linear, top left, top left, color-stop(0.7, #cca69a), color-stop(0.3, #9ad18b));
    background-image: -o-linear-gradient(top left, #cca69a 70%, #9ad18b 30%);
    background-image: -moz-linear-gradient(top left, #cca69a 70%, #9ad18b 30%);
    background-image: -webkit-linear-gradient(top left, #cca69a 70%, #9ad18b 30%);
    background-image: -ms-linear-gradient(top left, #cca69a 70%, #9ad18b 30%);
    background-image: linear-gradient(to top left, #cca69a 70%, #9ad18b 30%);
}

#preview-2 {
    background-image: -webkit-gradient( linear, right top, right top, color-stop(0.7, #9ad18b), color-stop(0.3, #cca69a));
    background-image: -o-linear-gradient(right top, #9ad18b 70%, #cca69a 30%);
    background-image: -moz-linear-gradient(right top, #9ad18b 70%, #cca69a 30%);
    background-image: -webkit-linear-gradient( right top, #9ad18b 70%, #cca69a 30%);
    background-image: -ms-linear-gradient(right top, #9ad18b 70%, #cca69a 30%);
    background-image: linear-gradient(to right top, #9ad18b 70%, #cca69a 30%);
}

#preview-3 {
    background-image: -webkit-gradient( linear, right top, right top, color-stop(0.7, #9ad18b), color-stop(0.3, #cca69a));
    background-image: -o-linear-gradient(right top, #9ad18b 70%, #cca69a 30%);
    background-image: -moz-linear-gradient(right top, #9ad18b 70%, #cca69a 30%);
    background-image: -webkit-linear-gradient( right top, #9ad18b 70%, #cca69a 30%);
    background-image: -ms-linear-gradient(right top, #9ad18b 70%, #cca69a 30%);
    background-image: linear-gradient(to right top, #9ad18b 70%, #cca69a 30%);
}

.box_index {
    pointer-events: auto;
    z-index: 999999;
}

.box__img--original {
    max-height: calc(100vh - 10rem);
    filter: none;
}

.overlay__close {
    position: absolute;
    top: 0;
    left: 0;
    background: none;
    border: 0;
    // margin: 1rem;
    padding: 1.5rem 1rem 0rem 2rem;
    opacity: 0;
    z-index: 1000;
    color: #000;
    -webkit-backdrop-filter: blur(4px);
    backdrop-filter: blur(4px);
    width: 100%;
    &:focus {
        outline: none;
    }
}

.overlay__content {
    font-size: 1rem;
    line-height: 1.5;
    max-width: 25rem;
    margin: 1rem 0 0 10vw;
}

.overlay__content_image_one {
    font-size: 1rem;
    line-height: 1.5;
    max-width: 25rem;
    margin: 1rem 0 0 10vw;
}

.overlay__content_image_three {
    font-size: 0.9rem;
    line-height: 1.5;
    max-width: 25rem;
    margin: 1rem 0 0 10vw;
}

@include breakpoint(desktop) {
    .grid {
        position: relative;
        top: 0rem;
    }
}

@media screen and (min-width: 55em) {
    .grid {
        display: grid;
        align-items: center;
        padding: 1rem 3rem 0rem 3rem;
        // grid-row-gap: 2rem;
        grid-template-columns: repeat(3, calc(100% / 3));
    }
    .grid__item {
        margin: 0;
    }
    .box {
        margin: 4rem;
        z-index: 999;
        margin-bottom: 2rem;
    }
    .box__title {
        top: -4rem;
        right: -4rem;
        -webkit-writing-mode: vertical-rl;
        writing-mode: vertical-rl;
    }
    .box__title_four {
        top: -6rem;
        right: 0.5rem;
    }
    .box__title--straight {
        -webkit-writing-mode: horizontal-tb;
        writing-mode: horizontal-tb;
        .box__title-inner {
            &::before {
                width: 0;
                height: auto;
            }
        }
    }
    .box__title--bottom_image_one {
        bottom: -2.7rem;
        top: auto;
    }
    .box__title--bottom-six {
        top: 7rem;
        right: -7rem;
    }
    .box__title--left {
        left: -2rem;
        right: auto;
    }
    .box__text--topcloser {
        top: -5rem;
    }
    .box__text--bottom {
        bottom: -5rem;
        top: auto;
    }
    .box__text--bottomcloser {
        bottom: -5rem;
        top: auto;
    }
    .box__text--right {
        right: 0;
        left: auto;
    }
    .box__text-inner--rotated1 {
        transform: rotate(4deg);
    }
    .box__text-inner--rotated2 {
        transform: rotate(-3deg);
    }
    .box__text-inner--rotated3 {
        transform: rotate(-15deg);
    }
    .box__deco {
        display: block;
    }
    .box__deco--left {
        right: auto;
        left: -3rem;
    }
    .box__deco--bottom {
        left: 7.2rem;
        bottom: 0rem;
    }
    .box__deco--top {
        top: 0;
        bottom: auto;
    }
    .overlay__item {
        // display: block;
        // flex-direction: row;
        .box__title--bottom {
            bottom: 0;
        }
    }
    .overlay__content {
        font-size: 2.25rem;
        margin-top: 0;
    }
    .overlay__content_image_one {
        font-size: 1.2rem;
        margin-top: 0;
    }
    .overlay__content_image_three {
        font-size: 1rem;
        margin-top: 0;
    }
}

@media screen and (max-width: 600px) {
    .sub_title {
        padding: 1rem;
        font-size: 15px;
        text-align: justify;
    }
    .box__title {
        color: $color_10;
    }
    .overlay__content_image_one {
        margin: 10px 0px;
    }
    .overlay__content_image_three {
        margin: 10px 0px;
    }
    .box__title-inner {
        font-size: 2.3rem;
    }
}

@media screen and (max-width: 992px) {
    .overlay__content_image_three {
        font-size: 1rem;
    }
    .overlay__content_image_one {
        font-size: 1.1rem;
    }
    .box__content {
        top: 0px;
        color: $color_10;
        text-shadow: 0.3px 0.3px 0.8px #000;
        font-size: 1.2rem;
    }
}